<template>
    <div class="search-result-contenter" ref='container' @touchmove.stop='blurSearch'>
      <!-- 搜索框 -->
      <div class="search_box" v-show="show">
        <van-nav-bar left-arrow @click-left="goBack">
          <slot name="left" slot="left"><i class="icon-back"></i></slot>
          <slot name="title" slot="title">
            <div style='display:flex;flex-direction:row;align-items:center;height:0.92rem;'>
                <van-field
                  v-show="showInput"
                  ref='search'
                  left-icon="search"
                  v-model="keyword"
                  show-action
                  shape="round"
                  maxlength="20"
                  inputmode='search'
                  :placeholder="placeholder"
                  @keyup.enter="doSearch"
                  @clear="doClear"
                  @input="doInput"
                  @focus="pauseAnimation"
                  @blur="showPlaceholders"
                  class='search-filed'
                  :clearable="true"
                >
                  <div v-show="showHolders" @click="clickPlaceholders()" slot='right-icon' ref='flip' class='flip-text' :style='animationStatus'>
                      <div>{{flipHolders[0]}}</div>
                      <div>{{flipHolders[1]}}</div>
                  </div>
                </van-field>
              <div @click="doSearch" style='padding:0 0.23rem;font-size:0.27rem'>
                  搜索
              </div>
            </div>
          </slot>
        </van-nav-bar>
      </div>
      <div class="search_content">
        <!-- 点击综合弹层 -->
        <template>
          <!-- 热门搜索 -->
          <div class="hot-wrap" v-if="!storeId">
            <hot-search :hotSearchList="hotSearchList" @doSearch="doSearch" />
          </div>
          <!-- 最近搜索记录 -->
          <div :class="{'history-wrap':true}" >
            <history-search @doSearch="doSearch" :show='show'/>
          </div>
          <div v-if="!storeId">
            <top-search @doSearch="doSearch" v-if="topSearchList&&topSearchList.length>0" :topSearchList="topSearchList" />
          </div>
        </template>
        <key-search v-show="keySearchShowFlag" :keyword="keyword" :keywordsList="keywordsList" @doSearch="doSearch" />
      </div>
    </div>
</template>

<script>
// import utils from '@/utils/utils'
import { defaultSearch, getHotSearchProductList, searchDropdown, hotRecommend } from '@/http/service/search'
const hotSearch = () => import('./components/hot-search')
const historySearch = () => import('./components/history-search')
const topSearch = () => import('./components/top-search')
const keySearch = () => import('./components/key-search')

export default {
  props: {
    'show': { type: Boolean, default: false },
    'words': { type: String, default: '' },
    'defaultRoute': { type: Boolean, default: false },
    'storeId': { type: String, default: '' },
    'hiddenId': { type: String, default: '' },
    'from': { type: String, default: 'normal' },
  },
  components: {
    hotSearch,
    historySearch,
    keySearch,
    topSearch,
  },
  data() {
    return {
      showInput: false,
      showHolders: true,
      animationStatus: {},
      placeholder: '', // 输入框的默认值
      placeholders: [], // 输入框holders
      currentIndex: 0, // 当前holder的索引值
      keySearchShowFlag: false, // 关键词展示框
      hotSearchList: [], // 存放热门搜索数据
      keywordsList: [], // 存放关键词的数据
      topSearchList: [], // 热搜榜数据
      keyword: '', // 绑定的输入框的值
    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
    flipHolders() {
      // 把多元素数组取出2个元素
      if (this.currentIndex === this.placeholders.length - 1) {
        return [this.placeholders[this.currentIndex], this.placeholders[0]];
      }
      return this.placeholders.filter((cv, index, arr) => {
        return index === this.currentIndex || index === this.currentIndex + 1;
      })
    }
  },
  watch: {
    show: {
      handler: function(v, ov) {
        this.keyword = this.words;
        this.showInput = true
        this.$nextTick(() => {
          this.$refs.search.focus();
          this.showHolders = false;
        })
        if (this.hiddenId && document.querySelector(`#${this.hiddenId}`)) {
          if (v) {
            document.querySelector(`#${this.hiddenId}`).style.display = 'none';
          } else {
            document.querySelector(`#${this.hiddenId}`).style.display = '';
          }
        }
      },
      immediate: true
    }
  },
  methods: {
    blurSearch() {
      this.$refs.search.blur();
    },
    // 向父组件提交搜索关键词
    doSearch(keyword) {
      // TODO: 加防抖
      this.$refs.search.focus();
      this.$refs.search.blur();
      this.keySearchShowFlag = false;
      if (keyword && keyword.keyword && typeof keyword.keyword === 'string') {
        if (keyword.type === 'history' || keyword.type === 'hot' || keyword.type === 'key') {
          this.keyword = keyword.keyword
        }
        if (keyword.type === 'alreayRoute') {
          if (this.from !== 'goodsSearch') {
            setTimeout(() => {
              this.$emit('search', { 'alreayRoute': true });
            }, 1000);
          } else {
            this.$emit('search', { 'alreayRoute': true });
          }
          if (this.from !== 'goodsSearch' && this.hiddenId && document.querySelector(`#${this.hiddenId}`)) {
            document.querySelector(`#${this.hiddenId}`).style.display = '';
          }
          return;
        }
      }
      this.keyword = this.keyword || this.placeholder || this.placeholders[this.currentIndex]
      this.showHolders = false;
      this.showInput = false
      if (this.defaultRoute && this.keyword) {
        if (this.hiddenId && document.querySelector(`#${this.hiddenId}`)) {
          document.querySelector(`#${this.hiddenId}`).style.display = '';
        }
        this.$router.push({
          path: '/goodsSearch',
          query: {
            keyword: this.keyword,
            visitStore: this.storeId || '',
          },
        })
      }
      this.$emit('search', this.keyword);
    },
    // 向父组件提交返回键事件
    goBack() {
      this.$emit('go-back');
      this.showInput = false
    },
    listenerEventCallback() {
      this.currentIndex = this.currentIndex >= this.placeholders.length - 1 ? 0 : this.currentIndex + 1
    },
    pauseAnimation() {
      this.animationStatus = {
        'animation-play-state': 'paused',
        '-webkit-animation-play-state': 'paused'
      }
      if (this.placeholder?.length === 0) {
        this.placeholder = this.placeholders[this.currentIndex];
      }
      this.showHolders = false;
    },
    clickPlaceholders() {
      this.$refs.search.focus()
      this.pauseAnimation();
    },
    resumeAnimation() {
      this.animationStatus = {}
      this.placeholder = ''
      this.showHolders = true;
    },
    showPlaceholders() {
      if (this.keyword === '') {
        if (this.placeholders.length > 1) {
          this.placeholder = '';
          this.showHolders = true;
          this.resumeAnimation()
        } else if (this.placeholders.length === 1) {
          this.placeholder = this.placeholders[0];
        }
      }
    },
    scrollList(e) {
      this.showToTopFlag = e.target.scrollTop > 150
    },
    changeAddr() {
      this.addrShow = true
    },
    onHideAddr() {
      this.addrShow = false
    },
    handleLocation() {
      this.locationshow = false
      this.addrShow = false
    },
    otherLocationOption() {
      this.locationshow = true
    },
    onHideLocation() {
      this.locationshow = false
    },
    closeAndBack() {
      this.locationshow = false
    },
    /**
     * @description 获取输入框的默认值
     */
    defaultSearch() {
      defaultSearch({
        platform: 3,
      }).then(res => {
        if (res.data.length > 1) {
          this.placeholders = res.data
          this.placeholder = res.data[0]
          this.showHolders = false;
        } else if (res.data.length === 1) {
          this.placeholder = res.data[0]
          this.showHolders = false;
        } else {
          this.placeholder = '请输入搜索内容'
          this.showHolders = false;
        }
      }).catch(_ => {
        this.placeholder = '请输入搜索内容'
        this.showHolders = false;
      })
    },
    /**
     * @description 获取热门搜索
     */
    hotRecommend() {
      hotRecommend({
        platform: 3,
      }).then(res => {
        this.hotSearchList = res.data
      })
    },
    /**
     * @description 监听搜索输入
     */
    doInput(value) {
      if (value.length > 0) {
        this.keySearchShowFlag = true
        // 清空原数组
        this.keywordsList = []
        this.searchDropdown()
      } else {
        this.keySearchShowFlag = false
        this.searchFlag = false
      }
    },
    /**
     * @description 获取关键词推荐
     */
    searchDropdown() {
      searchDropdown({
        key: this.keyword,
      }).then(res => {
        this.keywordsList = res.data
      })
    },
    /**
     * @description input框的清除按钮点击事件
     */
    doClear() {
      this.keyword = ''
      this.pauseAnimation();
    },
    /**
     * @description 获取热搜榜数据
     */
    topSearch() {
      getHotSearchProductList({
        provinceId: this.location.provinceId, // 省 id
        cityId: this.location.cityId, // 市 id
        regionId: this.location.districtId, // 区 id
        streetId: this.location.townshipId, // 街道 id
        // shareId: this.$store.getters.getShareId // 分享人id
      }).then(res => {
        if (res.success) {
          this.topSearchList = res.data
          console.log('热搜榜数据：')
          console.log(this.topSearchList)
        }
      })
    },
  },
  created() {
    if (this.words) {
      this.keyword = this.words;
    }
    this.$nextTick(() => {
      this.$refs.flip.removeEventListener('webkitAnimationIteration', this.listenerEventCallback);
      this.$refs.flip.addEventListener('webkitAnimationIteration', this.listenerEventCallback)
    })
    this.defaultSearch()
    this.hotRecommend()
    this.topSearch()
  },
  mounted() {
    console.log(this.$refs.search);
    this.$nextTick(() => {
      this.$refs.search.focus();
    })
  },
  activated() {
    this.$nextTick(() => {
      this.$refs.search.focus();
    })
  }
}
</script>

