<template>
  <div class="text-container">
    <transition class="" name="slide" mode="out-in">
      <p class="text" :key="text.id">{{text.val}}</p>
    </transition>
  </div>
</template>

<script>

export default {
  name: 'TextScroll',
  props: {
    dataList: {
      type: Array,
      default: () => []
    },
  },
  data() {
    return {
      count: 0, // 当前索引
      intervalId: null, // 定时器ID
      playTime: 2000, // 定时器执行间隔
    };
  },
  computed: {
    text() {
      return {
        id: this.count,
        val: this.dataList[this.count],
      }
    },
  },
  created() {
    this.intervalId = setInterval(() => { // 定义定时器
      this.getText();
    }, this.playTime);
  },
  methods: {
    getText() {
      const len = this.dataList.length; // 获取数组长度
      if (len === 0) {
        return; // 当数组为空时，直接返回
      }
      if (this.count === len - 1) { // 当前为最后一个时
        this.count = 0; // 从第一个开始
      } else {
        this.count++; // 自增
      }
    },
  },
  destroyed() {
    clearInterval(this.intervalId); // 清除定时器
  },
};
</script>

<style lang="scss" scoped>

.text-container{
  display: flex;
  align-items: center;
  height: .52rem;
  overflow: hidden;
  font-size: .28rem;
  -webkit-user-select: none;
}

.text {
  height: .52rem;
  display: flex;
  align-items: center;
  line-height: .6rem;
  font-size: inherit;
  color: #666666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.slide-enter-active, .slide-leave-active {
  transition: all .5s;
}

.slide-enter{
  transform: translateY(.42rem);
}

.slide-leave-to {
  transform: translateY(-.42rem);
}
</style>
